import React, { Component } from 'react';
import $script from 'scriptjs';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import moment from 'moment';
import './App.css';

const CLIENT_ID =
  '626897179786-u8rj411ic3irrm9lp5poj1gi0q8ckurf.apps.googleusercontent.com';
const API_KEY = 'AIzaSyB6G-3wgN7eBBJak-rl6EVvtkAmvSC5wVA';

const DISCOVERY_DOCS = [
  'https://sheets.googleapis.com/$discovery/rest?version=v4'
];

const SCOPES = 'https://www.googleapis.com/auth/spreadsheets';

class App extends Component {
  state = {
    transactionToSubmit: {
      employee: '',
      item: '',
      price: '',
      date: moment().format('YYYY-MM-DDThh:mm')
    },
    transactions: [],
    items: []
  };

  componentDidMount() {
    $script('https://apis.google.com/js/api.js', () => {
      this.handleClientLoad();
    });
  }

  handleClientLoad = () => {
    window.gapi.load('client:auth2', this.initClient);
  };

  initClient = () => {
    window.gapi.client
      .init({
        apiKey: API_KEY,
        clientId: CLIENT_ID,
        discoveryDocs: DISCOVERY_DOCS,
        scope: SCOPES
      })
      .then(() => {
        this.setState({ loaded: true });
        this.listMajors();
        this.fetchItems();
        window.gapi.auth2
          .getAuthInstance()
          .isSignedIn.listen(this.updateSigninStatus);

        this.updateSigninStatus(
          window.gapi.auth2.getAuthInstance().isSignedIn.get()
        );
      });
  };

  updateSigninStatus = isSignedIn => {
    this.setState({ isSignedIn });
  };

  handleAuthClick(event) {
    window.gapi.auth2.getAuthInstance().signIn();
  }

  handleSignoutClick(event) {
    window.gapi.auth2.getAuthInstance().signOut();
  }

  handleChange = field => event => {
    const transactionToSubmit = {
      ...this.state.transactionToSubmit,
      ...{ [field]: event.target.value }
    };

    if (field === 'item') {
      transactionToSubmit.price = this.state.items.find(
        i => i.name === event.target.value
      ).price;
    }

    this.setState({ transactionToSubmit });
  };

  handleSubmit = event => {
    event.preventDefault();
    const range = 'entrada!A1:G';
    const params = {
      spreadsheetId: '1NrLTgE3K_o8LYXdfH1dp4kDMAckoQZtlSaYZFGn8Ick',
      range,
      valueInputOption: 'USER_ENTERED'
    };

    const { employee, price, item, date } = this.state.transactionToSubmit;
    const formatedDate = moment(date, 'YYYY-MM-DDThh:mm').format(
      'DD/MM/YYYY h:mm:ss'
    );
    const valueRangeBody = {
      majorDimension: 'ROWS',
      range,
      values: [[employee, item, 'F', price, 'deve', 'ana', 30, formatedDate]]
    };

    const request = window.gapi.client.sheets.spreadsheets.values.append(
      params,
      valueRangeBody
    );
    request.then(
      response => {
        // TODO: Change code below to process the `response` object:
        console.log(response.result);
        this.listMajors();
      },
      reason => {
        console.error('error: ' + reason.result.error.message);
      }
    );
  };

  listMajors() {
    window.gapi.client.sheets.spreadsheets.values
      .get({
        spreadsheetId: '1NrLTgE3K_o8LYXdfH1dp4kDMAckoQZtlSaYZFGn8Ick',
        range: 'entrada!A2:H'
      })
      .then(
        response => {
          const transactions = response.result.values;
          this.setState({ transactions });
        },
        response => {
          this.setState({ error: response.result.error });
        }
      );
  }

  fetchItems = () => {
    window.gapi.client.sheets.spreadsheets.values
      .get({
        spreadsheetId: '1NrLTgE3K_o8LYXdfH1dp4kDMAckoQZtlSaYZFGn8Ick',
        range: 'servicos!A2:B'
      })
      .then(
        response => {
          const items = response.result.values.map(([name, price]) => ({
            name,
            price
          }));
          this.setState({ items });
        },
        response => {
          this.setState({ error: response.result.error });
        }
      );
  };

  lastTransactions = () => this.state.transactions.reverse().slice(0, 10);

  render() {
    if (!this.state.loaded) {
      return 'Carregando...';
    }
    return (
      <div className="App">
        <h1>Salão</h1>

        {this.state.isSignedIn ? (
          <button id="signout_button" onClick={this.handleSignoutClick}>
            Sign Out
          </button>
        ) : (
          <button id="authorize_button" onClick={this.handleAuthClick}>
            Authorize
          </button>
        )}

        <br />
        <br />
        <br />

        <form onSubmit={this.handleSubmit} noValidate autoComplete="off">
          <TextField
            select
            label="Item"
            value={this.state.transactionToSubmit.item}
            onChange={this.handleChange('item')}
            margin="normal"
          >
            {this.state.items.map(option => (
              <MenuItem key={option.name} value={option.name}>
                {option.name}
              </MenuItem>
            ))}
          </TextField>

          <TextField
            value={this.state.transactionToSubmit.price}
            label="Preço"
            type="number"
            onChange={this.handleChange('price')}
          />

          <TextField
            value={this.state.transactionToSubmit.date}
            label="date"
            type="datetime-local"
            onChange={this.handleChange('date')}
          />

          <TextField
            select
            label="Funcionario"
            value={this.state.transactionToSubmit.employee}
            onChange={this.handleChange('employee')}
            margin="normal"
          >
            {['Maurilio', 'Victor'].map(option => (
              <MenuItem key={option} value={option}>
                {option}
              </MenuItem>
            ))}
          </TextField>
          <br />
          <button type="submit">Salvar</button>
        </form>
        <ul>
          {this.lastTransactions().map((transaction, key) => (
            <li key={key}>{transaction.join(',')}</li>
          ))}
        </ul>
      </div>
    );
  }
}

export default App;



